import React from "react";
import Metadata from "./Metadata";

function Provenance()  {
    return (
        <section id = "gnft-provenance">
            <div class = "container">
                <div class = "row justify-content-center align-items-center">
                    <div class = "col-12">
                        <div class = "section-1 mb-5">
                            <h1>Gratitude NFT Provenance Record</h1>
                            <p>This page presents the provenance record of each Gratitude NFT that will ever exist. This is the final provenance record stored on the smart contract. Here is the relevant information:</p>
                            <div class = "important-info">
                                <ul>
                                    <li>
                                        Gratitude NFT Contract Address | <a href = "https://rinkeby.etherscan.io/address/0x028dA691A4d13E83dfbf42b94103bB21CA33aB84" target = "_blank">0x028dA691A4d13E83dfbf42b94103bB21CA33aB84</a>
                                    </li>
                                    <li>
                                        Provenance | QmWeGPZFsKiYLNMuuwJcWCzAfHXuMN53FtwY4Wbij8ZVHG
                                    </li>
                                </ul>
                            </div>
                        </div>
                        <div class = "section-2">
                            <h2 class = "mt-2">The table below lists the original index, assigned name, edition, dna and IPFS hash.</h2>
                            <ul class = "mt-2">
                                <li>
                                    Name | Edition | DNA | IPFS Hash
                                </li>
                            </ul>
                            <table class = "mt-2">
                                <tbody>

                                {
                                    Metadata.map(post => {
                                        return (
                                            <>
                                            <tr>
                                                <th>{post.name}</th>
                                                <th>{post.edition}</th>
                                                <th>{post.dna}</th>
                                                <th class = "ipfs-hash-provenance"><a href ={post.image} target="_blank">{post.image}</a></th>
                                            </tr>
                                            </>
                                        )
                                    })
                                }
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Provenance;
