import React from "react";
import Metadata from "./Metadata";
import { LazyLoadImage } from 'react-lazy-load-image-component';
import 'react-lazy-load-image-component/src/effects/blur.css';

const backgroundUnique = [...new Set(Metadata.map(post => post.attributes[0].value))];
const leafUnique = [...new Set(Metadata.map(post => post.attributes[1].value))];
const petalsUnique = [...new Set(Metadata.map(post => post.attributes[2].value))];
const eyesUnique = [...new Set(Metadata.map(post => post.attributes[3].value))];
const eyesAccessoriesUnique = [...new Set(Metadata.map(post => post.attributes[4].value))];
const headAccessoriesUnique = [...new Set(Metadata.map(post => post.attributes[5].value))];
const neckAccessoriesUnique = [...new Set(Metadata.map(post => post.attributes[6].value))];
const mainAccessoriesUnique = [...new Set(Metadata.map(post => post.attributes[7].value))];


function Gallery()  {

    var i = 0;

    return (
        <section id="gnft-gallery" data-aos="fade-up">
            <div class  = "container">
                <h2>Gallery</h2>
                <div class = "row">
                    <div class = "attribute-select-wrapper col-12 col-md-3 d-flex flex-column mt-3">
                        <select id = "attribute-select-background" class = "attribute-select">
                            <option value="Orange" class= "d-none"  selected>Background</option>
                            {
                                backgroundUnique.map(post => {
                                    return (
                                        <>
                                            <option value = {post}>{post}</option>
                                        </>
                                    )
                                })

                            }
                        </select>
                        <select id = "attribute-select-leaf" class = "attribute-select  mt-2">
                            <option value="Green" class = "d-none" selected>Leaf</option>
                            {
                                leafUnique.map(post => {
                                    return (
                                        <>
                                            <option value = {post}>{post}</option>
                                        </>
                                    )
                                })

                            }
                        </select>
                        <select id = "attribute-select-petals" class = "attribute-select mt-2">
                            <option class = "d-none" value="Petal Pink" selected>Petals</option>
                            {
                                petalsUnique.map(post => {
                                    return (
                                        <>
                                            <option value = {post}>{post}</option>
                                        </>
                                    )
                                })

                            }
                        </select>
                        <select id = "attribute-select-eyes" class = "attribute-select mt-2">
                            <option value="Brown" selected>Eyes</option>
                            {
                                eyesUnique.map(post => {
                                    return (
                                        <>
                                            <option value = {post}>{post}</option>
                                        </>
                                    )
                                })

                            }
                        </select>
                        <select id = "attribute-select-eyes-acc" class = "attribute-select mt-2">
                            <option class = "d-none" value="None" selected>Eyes Accessories</option>
                            {
                                eyesAccessoriesUnique.map(post => {
                                    return (
                                        <>
                                            <option value = {post}>{post}</option>
                                        </>
                                    )
                                })

                            }
                        </select>
                        <select id = "attribute-select-head-acc" class = "attribute-select mt-2">
                            <option class = "d-none" value="None" selected>Head Accessories</option>
                            {
                                headAccessoriesUnique.map(post => {
                                    return (
                                        <>
                                            <option value = {post}>{post}</option>
                                        </>
                                    )
                                })

                            }
                        </select>
                        <select id = "attribute-select-neck-acc" class = "attribute-select mt-2">
                            <option class = "d-none" value="Chain ETH" selected>Neck Accessories</option>
                            {
                                neckAccessoriesUnique.map(post => {
                                    return (
                                        <>
                                            <option value = {post}>{post}</option>
                                        </>
                                    )
                                })

                            }
                        </select>
                        <select id = "attribute-select-main-acc" class = "attribute-select mt-2">
                            <option class = "d-none" value="Basketball Basic" selected>Main Accessories</option>
                            {
                                mainAccessoriesUnique.map(post => {
                                    return (
                                        <>
                                            <option value = {post}>{post}</option>
                                        </>
                                    )
                                })

                            }
                        </select>
                        {/* <input id= "attribute-input-id" class = "attribute-select mt-2" type = "number" min = "0" max = "2222" placeholder = "By ID"></input> */}
                    </div>
                    <div class = "col-12 col-md-9">
                        <div class = "row">
                            {
                                Metadata.map(post => {
                                    return (
                                        <>
                                        <div id = {"gallery-id-" +post.edition} class = "gallery-img-wrapper col-12 col-md-6 col-lg-4 mt-3" data-background={post.attributes[0].value} data-leaf={post.attributes[1].value}  data-petals={post.attributes[2].value} data-eyes={post.attributes[3].value} data-eyes-accessories={post.attributes[4].value} data-head-accessories={post.attributes[5].value} data-neck-accessories={post.attributes[6].value} data-main-accessories={post.attributes[7].value} data-gratitude-id={post.edition}>
                                            <div class = "img-wrapper">
                                                <LazyLoadImage height="100%" effect = "blur" class = "img-fluid" src = {post.image}   alt = {post.name} />
                                            </div>
                                            <h3 class = "mt-3">{post.name}</h3>
                                        </div>
                                        </>
                                    )
                                })
                            }
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Gallery;
