import React from "react";

function ProvenanceSection()  {
    return (
        <section id = "gnft-provenance-section">
            <div class = "container text-center">
                <h2>Provenance</h2>
                <p class = "mt-2">Our provenance record represents the authenticity of each Gratitude NFT that will ever exist. </p>
                <div class = "button-wrapper mb-2">
                    <a href = "/provenance" class = "provenance-button">See Provenance Page</a>
                </div>
            </div>
        </section>
    )
}

export default ProvenanceSection;
