import React, { useEffect, useState, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { connect } from "./redux/blockchain/blockchainActions";
import { fetchData } from "./redux/data/dataActions";
import * as s from "./styles/globalStyles";
import styled from "styled-components";

const truncate = (input, len) =>
  input.length > len ? `${input.substring(0, len)}...` : input;

export const StyledButton = styled.button`
  padding: 12px 8px;
  background-color: #9456ff;
  border-radius: 15px;
  border: none;
  padding: 10px;
  font-size: 18px;
  font-family: 'rajdhani-bold';
  font-weight: bold;
  color: #ffffff;
  width: 180px;
  cursor: pointer;
  transition: all .3s ease;
  box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
  -webkit-box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
  -moz-box-shadow: 0px 6px 0px -2px rgba(250, 250, 250, 0.3);
  :active {
    box-shadow: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
  }
  :hover {
    background-color: #8248e5;
  }
`;

export const StyledRoundButton = styled.button`
  padding: 10px;
  border-radius: 100%;
  border: none;
  background-color: #9456ff;
  padding: 10px;
  font-weight: bold;
  font-size: 15px;
  color: var(--accent);
  width: 30px;
  height: 30px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 4px 0px -2px rgba(250, 250, 250, 0.3);
  -webkit-box-shadow: 0px 4px 0px -2px rgba(250, 250, 250, 0.3);
  -moz-box-shadow: 0px 4px 0px -2px rgba(250, 250, 250, 0.3);
  :active {
    box-shadow: none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
  }
  :hover {
    background-color: #8248e5;
  }
`;

export const ResponsiveWrapper = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: stretched;
  align-items: stretched;
  width: 100%;
  @media (min-width: 767px) {
    flex-direction: row;
  }
`;

export const StyledLogo = styled.img`
  width: 200px;
  @media (min-width: 767px) {
    width: 300px;
  }
  transition: width 0.5s;
  transition: height 0.5s;
`;

export const StyledIcon = styled.img`
  width: 120px;
  transition: width 0.5s;
  transition: height 0.5s;
`;

export const StyledImg = styled.img`
  box-shadow: 0px 5px 11px 2px rgba(0, 0, 0, 0.7);
  border: 4px dashed var(--secondary);
  background-color: var(--accent);
  border-radius: 100%;
  width: 200px;
  @media (min-width: 900px) {
    width: 250px;
  }
  @media (min-width: 1000px) {
    width: 300px;
  }
  transition: width 0.5s;
`;

export const StyledLink = styled.a`
  color: #0a0a0a;
  text-decoration: none;
`;

function Ambassador() {
  const dispatch = useDispatch();
  const blockchain = useSelector((state) => state.blockchain);
  const data = useSelector((state) => state.data);
  const [claimingNft, setClaimingNft] = useState(false);
  const [feedback, setFeedback] = useState(`Fill out the information below.`);
  const [proofValidity, setProofValidity] = useState(`0x`);
  const [URIValidity, setURIValidity] = useState(`https://ipfs.io/ipfs/`);
  const [checkAmbassadorValidity, setCheckAmbassadorValidity] = useState("true");
  const [mintAmount, setMintAmount] = useState(1);
  const [CONFIG, SET_CONFIG] = useState({
    CONTRACT_ADDRESS: "",
    SCAN_LINK: "",
    NETWORK: {
      NAME: "",
      SYMBOL: "",
      ID: 0,
    },
    NFT_NAME: "",
    SYMBOL: "",
    MAX_SUPPLY: 1,
    WEI_COST: 0,
    DISPLAY_COST: 0,
    GAS_LIMIT: 0,
    MARKETPLACE: "",
    MARKETPLACE_LINK: "",
    SHOW_BACKGROUND: false,
  });

  const claimNFTs = () => {
    let cost = 0;
    let gasLimit = CONFIG.GAS_LIMIT;
    let totalCostWei = String(cost * mintAmount);
    let totalGasLimit = String(gasLimit * mintAmount);
    console.log("Claim Code:", proofValidity);
    console.log("URI Validity:", URIValidity);
    console.log("Ambasaddor:", checkAmbassadorValidity)
    console.log("Cost: ", totalCostWei);
    console.log("Gas limit: ", totalGasLimit);
    setFeedback(`Minting your ${CONFIG.NFT_NAME}...`);
    setClaimingNft(true);
    blockchain.smartContract.methods
      .redeem(URIValidity,checkAmbassadorValidity,proofValidity)
      .send({
        gasLimit: String(totalGasLimit),
        to: CONFIG.CONTRACT_ADDRESS,
        from: blockchain.account,
      })
      .once("error", (err) => {
        console.log(err);
        setFeedback("Sorry, something went wrong. Please make sure that you pasted the claim code correctly.");
        setClaimingNft(false);
      })
      .then((receipt) => {
        console.log(receipt);
        setFeedback(
          `Congratulations! Thank you for beginning your NFT journey with GRATITUDE GANG. Head to OpenSea and check out your personal sunflower!`
        );
        hideForm();
        setClaimingNft(false);
        dispatch(fetchData(blockchain.account));
      });
  };

  function hideForm() {
    jQuery('.to-hide').hide();
  }

  const changeCheckAmbassadorValidity  =  () => {{
    let newCheckAmbassadorValidity = event.target.value;
    setCheckAmbassadorValidity(newCheckAmbassadorValidity); 
  }}


  const changeProofValidity  =  () => {{
    let newProofValidity = event.target.value;
    setProofValidity(newProofValidity);
  }}

  // function getMetadata(checkAmbassador, checkClaimCode) {
  //   jQuery.getJSON(URIValidity, function(data) {
  //     var jsonObj = data;
  //     checkAmbassador = data.
  //   });
  // }

  const changeURIValidity  =  () => {{
    let newURIValidity = event.target.value;
    setURIValidity(newURIValidity);
    //getMetadata();
  }}

  const decrementMintAmount = () => { 
    let newMintAmount = mintAmount - 1;
    if (newMintAmount < 1) {
      newMintAmount = 1;
    }
    setMintAmount(newMintAmount);
  };

  const incrementMintAmount = () => {
    let newMintAmount = mintAmount + 1;
    if (newMintAmount > 5) {
      newMintAmount = 5;
    }
    setMintAmount(newMintAmount);
  };

  const getData = () => {
    if (blockchain.account !== "" && blockchain.smartContract !== null) {
      dispatch(fetchData(blockchain.account));
    }
  };

  const getConfig = async () => {
    const configResponse = await fetch("/config/config.json", {
      headers: {
        "Content-Type": "application/json",
        Accept: "application/json",
      },
    });
    const config = await configResponse.json();
    SET_CONFIG(config);
  };

  useEffect(() => {
    getConfig();
  }, []);

  useEffect(() => {
    getData();
  }, [blockchain.account]);

  return (
    <s.Screen>
      <s.Container
        flex={1}
        ai={"center"}
      >
        <s.SpacerSmall />
        <ResponsiveWrapper flex={1}>
          <s.SpacerLarge />
          <s.Container
            flex={2}
            jc={"center"}
            ai={"center"}
            style={{
              height: "fit-content"
            }}
          >
            <StyledIcon alt={"gratitudenft"} src={"/config/images/minting-icon.png"} />
            <s.TextTitle
              style={{
                textAlign: "center",
                fontSize: "36px",
                fontWeight: "bold",
                color: "#9456ff",
              }}
            >
              {data.totalSupply} / {CONFIG.MAX_SUPPLY}
            </s.TextTitle>
            <s.TextDescription
              style={{
                textAlign: "center",
                color: "#0a0a0a",
              }}
            > 
              Our Contract Address: <br/>
              <StyledLink target={"_blank"} href={CONFIG.SCAN_LINK}>
                 {truncate(CONFIG.CONTRACT_ADDRESS, 999)}
              </StyledLink>
            </s.TextDescription>
            <s.SpacerSmall />
            {Number(data.totalSupply) >= CONFIG.MAX_SUPPLY ? (
              <>
                <s.TextTitle
                  style={{ textAlign: "center", color: "var(--accent-text)" }}
                >
                  The sale has ended.
                </s.TextTitle>
                <s.TextDescription
                  style={{ textAlign: "center", color: "var(--accent-text)" }}
                >
                  You can still find {CONFIG.NFT_NAME} on
                </s.TextDescription>
                <s.SpacerSmall />
                <StyledLink target={"_blank"} href={CONFIG.MARKETPLACE_LINK}>
                  {CONFIG.MARKETPLACE}
                </StyledLink>
              </>
            ) : (
              <>
                <s.TextTitle
                  style={{ textAlign: "center", color: "#9456ff", fontFamily: "rajdhani-bold", fontSize: "20px" }}
                >
                  1 {CONFIG.NFT_NAME} ({CONFIG.SYMBOL}) costs {CONFIG.DISPLAY_COST}{" "}
                  {CONFIG.NETWORK.SYMBOL}.
                </s.TextTitle>
                <s.SpacerXSmall />
                <s.TextDescription
                  style={{ textAlign: "center", color: "var(--accent-text)" }}
                >
                  Excluding gas fees.
                </s.TextDescription>
                
                <s.SpacerSmall />
                {blockchain.account === "" ||
                blockchain.smartContract === null ? (
                  <s.Container ai={"center"} jc={"center"}>
                    <s.TextDescription
                      style={{
                        textAlign: "center",
                        color: "var(--accent-text)",
                      }}
                    >
                      Connect to the {CONFIG.NETWORK.NAME} network
                    </s.TextDescription>
                    <s.SpacerSmall />
                    <StyledButton
                      onClick={(e) => {
                        e.preventDefault();
                        dispatch(connect());
                        getData();
                      }}
                    >
                      Connect
                    </StyledButton>
                    {blockchain.errorMsg !== "" ? (
                      <>
                        <s.SpacerSmall />
                        <s.TextDescription
                          style={{
                            textAlign: "center",
                            color: "#FF0000",
                          }}
                        >
                          {blockchain.errorMsg}
                        </s.TextDescription>
                      </>
                    ) : null}
                  </s.Container>
                ) : (
                  <>
                    <s.TextDescription
                      style={{
                        textAlign: "center",
                        color: "var(--accent-text)",
                      }}
                    >
                      {feedback}
                    </s.TextDescription>

                    <div class = "form-for-ambassador to-hide">
                      {/* Put Input for Validity Proof Starts Here */}
                      <label  classs = "mt-3 " for = "proof-key">Claim Code:</label>
                      <input class = "claim-code-input" autocomplete = "off" placeholder = "Input Claim Code" type="text" id="proof-key" name="proof-key" onChange={(e) => {
                            e.preventDefault();
                            changeProofValidity();
                          }}></input>
                      {/* Put Input for Validity Proof Ends Here */}

                      {/* Put Input for URI Proof Starts Here */}
                      <label class = "mt-3" for = "uri-key">URI:</label>
                      <input class = "uri-input" autocomplete = "off" placeholder = "Input URI" type="text" id="uri-key" name="uri-key" onChange={(e) => {
                            e.preventDefault();
                            changeURIValidity();
                            
                          }}></input>
                      {/* Put Input for URI Proof Ends Here */}

                      {/* Dropdown for Ambassador Check Starts Here */}
                      <label class = "mt-3" for = "check-ambassador-key">Are you an ambassador?:</label>
                      <select class = "check-ambassador-dropdown" name="check-ambassador" id="check-ambassador" onChange={(e) => {
                            e.preventDefault();
                            changeCheckAmbassadorValidity();
                          }}>
                        <option value = "true">Yes</option>
                        <option value = "false">No</option>
                      </select>
                      {/* Dropdown for Ambassador Check Ends Here */}
                    </div>

                    <s.SpacerMedium />

                    <s.SpacerSmall />
                    <s.Container className = {"to-hide"} ai={"center"} jc={"center"} fd={"row"}>
                      <StyledButton
                        disabled={claimingNft ? 1 : 0}
                        onClick={(e) => {
                          e.preventDefault();
                          claimNFTs();
                          getData();
                        }}
                      >
                        {claimingNft ? "Busy" : "Mint"}
                      </StyledButton>
                    </s.Container>
                  </>
                )}
              </>
            )}
            <s.SpacerMedium />
          </s.Container>
          <s.SpacerLarge />
        </ResponsiveWrapper>
        <s.SpacerMedium />
      </s.Container>
    </s.Screen>
  );
}

export default Ambassador;

