import React from "react";

function NotFound()  {
    return (
        <section id = "gnft-not-found">
            <div class = "container">
                <div id = "wrap-not-found" class = "d-flex flex-column justify-content-center align-items-center text-center">
                    <img alt="gratitudenft" src="/config/images/minting-icon.png" />
                    <h1>404</h1>
                    <h2>Page Not Found</h2>
                    <p>We're sorry, the page you requested could not be found. Please go back to the homepage.</p>
                </div>
            </div>
        </section>
    )
}

export default NotFound;
